import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../Loader/QvipleLoader";
import CreateButton from "../../Ui/Button/CreateButton";
import FormInput from "../../Ui/Input/FormInput";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import Modal from "../../Ui/Modal/Modal";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useSubjectAddTimetable } from "../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import FeedDesiSubjectListDropdown from "./FeedDesiSubjectListDropdown";

const AddFeedSubjectTimetableDetailDayWise = ({
  day,
  onRefetch,
  onClose,
  openAs,
  menuData,
  onEdit,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    from: "",
    to: "",
    subject: "",
  });
  const [errorField, setErrorField] = useState("");
  const [subjectAddTimetable] = useSubjectAddTimetable();

  useEffect(() => {
    if (openAs === "EDIT") {
      setState({
        from: menuData?.from?.substring(0, 5) || "",
        to: menuData?.to?.substring(0, 5) || "",
        subject: menuData?.subject || "",
      });
    }
  }, [openAs, menuData]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSubjectSelect = (dt) => {
    setState((prev) => ({
      ...prev,
      subject: dt,
    }));
  };
  const onAddTimetable = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      if (openAs === "EDIT") {
        onEdit({
          from: timeFormatTable(state.from),
          to: timeFormatTable(state.to),
        });
      } else {
        if (day && state?.subject?._id) {
          setDisabled((pre) => !pre);
          subjectAddTimetable({
            sid: state?.subject?._id,
            bodyParameter: {
              from: timeFormatTable(state.from),
              to: timeFormatTable(state.to),
              day: day,
            },
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  // console.info("state", state, menuData);
  return (
    <>
      <Modal
        onClose={onClose}
        customBackdropStyle={{
          zIndex: "1500",
        }}
        customOverlayStyle={{
          zIndex: "1501",
        }}
      >
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>
                  {openAs === "EDIT"
                    ? t("upadate_time_table")
                    : t("add_new_time_table")}
                </h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FeedDesiSubjectListDropdown
              onSelect={onSubjectSelect}
              errorShow={errorField["subject"]}
              defalutValue={state?.subject}
              isDenied={openAs === "EDIT" ? true : false}
            />

            <FormInput
              label={t("from")}
              placeholder="00:00"
              name={"from"}
              value={state.from}
              type={"time"}
              onChange={onInputChange}
              errorShow={errorField["from"]}
            />
            <FormInput
              label={t("to")}
              placeholder="00:00"
              name={"to"}
              value={state.to}
              type={"time"}
              onChange={onInputChange}
              errorShow={errorField["to"]}
            />
            <CreateButton
              label={openAs === "EDIT" ? "update" : "add"}
              onAction={onAddTimetable}
            />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddFeedSubjectTimetableDetailDayWise;

const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  return `${hour}:${splitValue[1]} ${meridian}`;
};
