import { useState } from "react";
import FeedSubjectList from "./FeedSubjectList";
import FeedSubjectTimetableDetail from "./FeedSubjectTimetableDetail";

const FeedSubjectTimetable = ({ onClose }) => {
  const [step, setStep] = useState(2);
  const [state, setState] = useState(null);
  const onCloseStep = (order) => {
    setStep(order);
  };
  return (
    <>
      {step === 1 ? (
        <FeedSubjectList
          onClose={onClose}
          setState={setState}
          state={state}
          setStep={setStep}
        />
      ) : step === 2 ? (
        <FeedSubjectTimetableDetail
          // selectedSubject={state?.selectedSubject}
          // onClose={onCloseStep}
          onClose={onClose}
        />
      ) : null}
    </>
  );
};

export default FeedSubjectTimetable;
