import { useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate, useParams } from "react-router-dom";
import style from "./UserDashboard.module.css";
// import { useUserDashScheduleTimetableList } from "../../hooks/dashboard/dashboard-api";
import dayjs from "dayjs";
import Loading from "../Loader/Loading";
import { assestsFeedUrl } from "../Services/UrlConfig/AssestsBaseUrl";
import FeedSubjectTimetable from "./FeedSubjectTimetable/FeedSubjectTimetable";
import { useGetFeedStaffCurrentDaySchedule } from "../Hooks/ApiHooks/SubjectManager/subject-manager-api";

const SubjectTimetableSchedule = ({ uid }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const navigate = useNavigate();
  const params = useParams();

  const date = new Date();
  // const {
  //   userDashScheduleTimetableList,
  //   userDashScheduleTimetableListLoading,
  // } = useUserDashScheduleTimetableList({
  //   data: {
  //     sid: uid,
  //     date: dayjs(date).format("DD/MM/YYYY"),
  //     status: dayjs(date).format("dddd"),
  //     // date: "11/03/2024",
  //     // status: "Monday",
  //     flow: "Dashboard",
  //   },
  //   skip: !uid,
  // });

  const {
    getFeedStaffCurrentDaySchedule,
    getFeedStaffCurrentDayScheduleLoading,
  } = useGetFeedStaffCurrentDaySchedule({
    data: {
      sid: uid,
      date: dayjs(date).format("DD/MM/YYYY"),
      status: dayjs(date).format("dddd"),
      // date: "11/03/2024",
      // status: "Monday",
      flow: "Dashboard",
    },
    skip: !uid,
  });

  const onNavigate = useCallback(
    (staffId, data, lecture) => {
      navigate(
        `/q/${params.username}/member/subject/${data?.subjectName}?a=attendance`,
        {
          state: {
            onClickValue: data,
            sid: staffId,
            dashboardWhichLecture: lecture,
          },
        }
      );
    },
    [params.username, navigate]
  );

  const onCloseStep = () => {
    setStep("");
  };

  const onOpenSchedule = () => {
    setStep(1);
  };
  return (
    <>
      {getFeedStaffCurrentDaySchedule?.isStudent ? null : (
        <section className={style.stc_card}>
          <div className={style.stc_card_herder}>
            <h6>{t("today_lecture")}</h6>
            <img
              src={`${assestsFeedUrl}/setting.svg`}
              alt="setting icon"
              title="Timetable Schedule"
              onClick={onOpenSchedule}
            />
          </div>
          {getFeedStaffCurrentDaySchedule?.list?.map((schedule) => (
            <div
              className={style.stc_container}
              onClick={() =>
                onNavigate(
                  schedule?.assignStaff,
                  schedule?.subject,
                  schedule?.which_lecture
                )
              }
              key={schedule?._id}
            >
              <h6>
                {schedule?.from} To {schedule?.to}
              </h6>
              <h6>
                {schedule?.className} - {schedule?.classTitle}
              </h6>
              <p>
                {schedule?.subject?.selected_batch_query?.batchName ? (
                  <>
                    (
                    {schedule?.subject?.subject_category === "Practical"
                      ? "P:"
                      : "T:"}
                    {schedule?.subject?.selected_batch_query?.batchName}){" "}
                  </>
                ) : null}
                {schedule?.subject?.subjectName}{" "}
                <span
                  style={{
                    fontWeight: "400",
                    fontSize: "12px",
                  }}
                >
                  {schedule?.subject?.class?.batch?.batchName}
                </span>
              </p>
            </div>
          ))}
          {getFeedStaffCurrentDayScheduleLoading && <Loading />}
        </section>
      )}

      {step === 1 ? <FeedSubjectTimetable onClose={onCloseStep} /> : null}
    </>
  );
};

export default SubjectTimetableSchedule;
