import { useSelector } from "react-redux";
import {
  useUserStaffV1AllDesignationList,
  useUserV1ActivatedMember,
} from "../../Hooks/ApiHooks/UserMember/user-member-api";
// import Loading from "../../Loader/Loading";
import { useTranslation } from "react-i18next";
import CustomSelectSubject from "../../Ui/Select/CustomSelectSubject";
const FeedDesiSubjectListDropdown = ({
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();
  const userId = useSelector((state) => state.idChange.id);

  const { userV1ActivatedMember } = useUserV1ActivatedMember({
    data: {
      uid: userId,
      role: "",
    },
    skip: !userId,
  });
  const { userStaffV1AllDesignationList } = useUserStaffV1AllDesignationList({
    data: {
      sid: userV1ActivatedMember?.activeRole,
    },
    skip:
      userV1ActivatedMember?.member === "Student"
        ? true
        : !userV1ActivatedMember?.activeRole,
  });
  // console.info("userStaffV1AllDesignationList", userStaffV1AllDesignationList);

  return (
    <>
      <CustomSelectSubject
        selectLabel={t("select_subject")}
        selectedValue={t("please_select_subject")}
        options={
          userStaffV1AllDesignationList?.staff?.staffSubject?.length > 0
            ? userStaffV1AllDesignationList?.staff?.staffSubject
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />
    </>
  );
};

export default FeedDesiSubjectListDropdown;
