import { useTranslation } from "react-i18next";
import { useState } from "react";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import {
  useSubjectRemoveTimetable,
  useSubjectUpdateTimetable,
} from "../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import AddFeedSubjectTimetableDetailDayWise from "./AddFeedSubjectTimetableDetailDayWise";
const FeedSubjectTimetableDetailDayWiseMenu = ({
  onClose,
  onRefetch,
  menuData,
  ddid,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [subjectRemoveTimetable] = useSubjectRemoveTimetable();
  const [subjectUpdateTimetable] = useSubjectUpdateTimetable();

  const onDelete = () => {
    if (menuData?.ddid && menuData?._id) {
      setDisabled((pre) => !pre);
      subjectRemoveTimetable({
        ddid: menuData?.ddid,
        bodyParameter: {
          innerId: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?.ddid && menuData?._id && menuData?.subject) {
      setDisabled((pre) => !pre);
      onOpenEdit();
      subjectUpdateTimetable({
        ddid: menuData?.ddid,
        bodyParameter: {
          ...val,
          innerId: menuData?._id,
          subjectId: menuData?.subject,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  // console.info("dsfmnsd", menuData);

  return (
    <>
      {edit ? (
        <AddFeedSubjectTimetableDetailDayWise
          onRefetch={onRefetch}
          onClose={onOpenEdit}
          openAs="EDIT"
          menuData={menuData}
          onEdit={onEdit}
        />
      ) : (
        <ModalMenu
          onClose={onClose}
          disabled={disabled}
          customStyle={{
            width: "18rem",
          }}
          customBackdropStyle={{
            zIndex: "1500",
          }}
          customOverlayStyle={{
            zIndex: "1501",
          }}
        >
          <h6 onClick={onOpenEdit}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default FeedSubjectTimetableDetailDayWiseMenu;
