import { useSelector } from "react-redux";
import {
  useUserStaffV1AllDesignationList,
  useUserV1ActivatedMember,
} from "../../Hooks/ApiHooks/UserMember/user-member-api";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import QvipleLoader from "../../Loader/QvipleLoader";
import Loading from "../../Loader/Loading";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import stylePlate from "../../UserStaffMember/PlateformUser/PlateformUser.module.css";
import { useTranslation } from "react-i18next";
const FeedSubjectList = ({ onClose, setState, state, setStep }) => {
  const { t } = useTranslation();
  const userId = useSelector((state) => state.idChange.id);

  const { userV1ActivatedMember } = useUserV1ActivatedMember({
    data: {
      uid: userId,
      role: "",
    },
    skip: !userId,
  });
  const {
    userStaffV1AllDesignationList,
    userStaffV1AllDesignationListLoading,
  } = useUserStaffV1AllDesignationList({
    data: {
      sid: userV1ActivatedMember?.activeRole,
    },
    skip:
      userV1ActivatedMember?.member === "Student"
        ? true
        : !userV1ActivatedMember?.activeRole,
  });
  // console.info("userStaffV1AllDesignationList", userStaffV1AllDesignationList);

  const onSelectSubject = (data) => {
    setState((pre) => ({
      ...pre,
      selectedSubject: data,
    }));
    setStep(2);
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("subject_list")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {userStaffV1AllDesignationList?.staff?.staffSubject?.map(
              (subject, index) => (
                <div
                  className={stylePlate.assign_show_list_each}
                  key={`${subject?._id}-${index}`}
                  style={
                    state?.selectedSubject?._id === subject?._id
                      ? {
                          backgroundColor: "rgba(75, 26, 133, 0.1)",
                          border: "0.5px solid #cacaca",
                          marginBlock: "0.4rem",
                          borderRadius: "6px",
                          padding: "0.8rem 1rem",
                        }
                      : {
                          border: "0.5px solid #cacaca",
                          marginBlock: "0.4rem",
                          borderRadius: "6px",
                          padding: "0.8rem 1rem",
                        }
                  }
                  onClick={() => onSelectSubject(subject)}
                >
                  <div className={stylePlate.assign_each_paragraph}>
                    <h6>
                      {subject?.selected_batch_query?.batchName ? (
                        <>
                          (
                          {subject?.subject_category === "Practical"
                            ? "P:"
                            : "T:"}
                          {subject?.selected_batch_query?.batchName}){" "}
                        </>
                      ) : null}
                      {subject?.subjectName ?? ""}{" "}
                      {subject?.class?.classTitle ?? ""} {" - "}
                      <span
                        style={{
                          fontWeight: "400",
                          fontSize: "12px",
                        }}
                      >
                        {subject?.class?.batch?.batchName ?? ""}
                      </span>
                    </h6>
                  </div>
                </div>
              )
            )}
          </UserMemberContentWrapper>
          {userStaffV1AllDesignationListLoading && <Loading />}
        </div>
      </Modal>
    </>
  );
};

export default FeedSubjectList;
