import { useTranslation } from "react-i18next";
import style from "../AdmissionNotification.module.css";
import { assestsUserAdmissionUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { memo, useCallback, useState } from "react";
import { useUserDashboardActiveApplication } from "../../../Hooks/ApiHooks/UserMember/user-member-api";
import { useSelector } from "react-redux";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import StudentAdmissionFeesStatusReceipt from "../../../UserStaffMember/TransactionReceipt/Admission/StudentAdmissionFeesStatusReceipt";
import CreateButton from "../../../Ui/Button/CreateButton";
import StudentAdmissionPayFees from "../../../UserStaffMember/StudentAdmissionFees/PayFees/StudentAdmissionPayFees";
import { useNavigate, useParams } from "react-router-dom";
import { useStudentAdmissionFeeCardBankValidate } from "../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import CustomCreateButton from "../../../Ui/Button/CustomCreateButton";
import StudentHostelPayFees from "../../../UserStaffMember/StudentAdmissionFees/PayFees/StudentHostelPayFees";
const FeedCurrentAdmissionApplication = () => {
  const { t } = useTranslation();
  const uid = useSelector((state) => state.idChange.id);
  const [pay, setPay] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const navigate = useNavigate();
  const params = useParams();

  const [studentAdmissionFeeCardBankValidate] =
    useStudentAdmissionFeeCardBankValidate();
  const onNavigate = useCallback(() => {
    navigate(`/q/${params.username}/member/your/admission`);
  }, [params.username, navigate]);
  const {
    userDashboardActiveApplication,
    userDashboardActiveApplicationLoading,
  } = useUserDashboardActiveApplication({
    data: {
      uid: uid,
    },
    skip: !uid,
  });

  const onPayFees = () => {
    let fee_pay = userDashboardActiveApplication?.fees_payment?.[0];
    if (fee_pay?.applicationId?._id && fee_pay?.finance) {
      setDisabled((pre) => !pre);
      studentAdmissionFeeCardBankValidate({
        fid: fee_pay?.finance,
        appId: fee_pay?.applicationId?._id,
        flow: fee_pay?.is_hostel ? "BY_HOSTEL" : "BY_DEPARTMENT",
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          if (res?.data?.denied) {
            setSnackbar({
              open: true,
              msg: "Account is not activated",
              variant: "error",
            });
          } else {
            setPay(true);
          }
        })
        .catch({});
    }
  };

  const onApplicationUpdateForm = (data) => {
    if (
      data?.student?._id &&
      data?.applicationId?._id &&
      data?.instituteId?._id
    ) {
      navigate(`/q/${params.username}/edit/student`, {
        state: {
          openAs: "SELF_EDIT",
          sid: data?.student?._id,
          extraData: {
            regeneration_bool: "Yes",
            // appId: data?.applicationId?.applicationName,
            appId: data?.applicationId?._id,
            insId: data?.instituteId?._id,
            statusId: data?._id,
            whichAuthorized: "SELF_EDIT_APPLICATION",
          },
        },
      });
    }
  };

  // console.info(
  //   "userDashboardActiveApplication",
  //   userDashboardActiveApplication
  // );
  return (
    <>
      {userDashboardActiveApplicationLoading ? (
        ""
      ) : userDashboardActiveApplication?.conditional === "Not Show" ? null : (
        <div className={style.ufu_application}>
          <div className={style.ufu_your_application}>
            <div className={style.ufu_your_application_inner}>
              <h6 onClick={onNavigate}>
                {userDashboardActiveApplication?.latest_app?.applicationName ??
                  ""}
              </h6>

              {userDashboardActiveApplication?.fees_payment?.length > 0 ? (
                <div
                  className={style.ufu_your_application_inner}
                  style={{
                    flexDirection: "row",
                    justifyContent: "space-between",
                    marginBottom: "-1rem",
                  }}
                >
                  <p>Applicable Admission Fees: </p>
                  <h6>
                    Rs.{" "}
                    {userDashboardActiveApplication?.fees_payment?.[0]
                      ?.remaining_list?.applicable_card?.remaining_array?.[0]
                      ?.remainAmount ?? 0}
                  </h6>
                </div>
              ) : userDashboardActiveApplication?.document_verification
                  ?.length > 0 ? (
                <p>Submit your documents</p>
              ) : (
                <>
                  <p>
                    {userDashboardActiveApplication?.admission_application?.[0]
                      ?.rejection_modification === "Enable"
                      ? "Application Rejected"
                      : "Admission Application"}
                  </p>
                  {userDashboardActiveApplication?.admission_application?.[0]
                    ?.rejection_modification === "Enable" ? (
                    <p>
                      {t("reason")} {": "}{" "}
                      <span
                        style={{
                          fontWeight: "500",
                          color: "#121212",
                        }}
                      >
                        {userDashboardActiveApplication
                          ?.admission_application?.[0]?.rejection_reason ?? ""}
                      </span>
                    </p>
                  ) : null}
                </>
              )}
            </div>
          </div>

          <div
            style={{
              paddingInline: "1rem",
            }}
          >
            {userDashboardActiveApplication?.fees_payment?.length > 0 ? (
              <>
                {userDashboardActiveApplication?.fees_payment?.[0]
                  ?.payment_status === "Not Paid" ? (
                  <CreateButton
                    customStyle={{
                      marginTop: "0",
                    }}
                    label="pay_admission_fees"
                    onAction={onPayFees}
                  />
                ) : (
                  <StudentAdmissionFeesStatusReceipt
                    rid={
                      userDashboardActiveApplication?.fees_payment?.[0]
                        ?.fee_receipt?._id
                    }
                    receiptFile={
                      userDashboardActiveApplication?.fees_payment?.[0]
                        ?.fee_receipt?.receipt_file
                    }
                  />
                )}
              </>
            ) : (
              <>
                {userDashboardActiveApplication?.document_verification?.length >
                0 ? (
                  userDashboardActiveApplication?.admission_application?.[0]
                    ?.student?.application_print?.[0]?.value ? (
                    <a
                      href={`${pdfCdnUrl}/${userDashboardActiveApplication?.admission_application?.[0]?.student?.application_print?.[0]?.value}`}
                      title="Download Admission Application File"
                      target="_blank"
                      rel="noopener noreferrer"
                      // style={{
                      //   color: "#00f",
                      // }}
                    >
                      <div
                        className={style.ad_button}
                        style={{
                          marginTop: "0",
                        }}
                      >
                        Download Admission Application Form
                        <img
                          src={`${assestsUserAdmissionUrl}/download.svg`}
                          alt="download"
                        />
                      </div>
                    </a>
                  ) : null
                ) : (
                  <>
                    {userDashboardActiveApplication?.admission_application?.[0]
                      ?.rejection_modification === "Enable" ? (
                      <CustomCreateButton
                        label="kindly_update_the_details_and_reapply"
                        onAction={() =>
                          onApplicationUpdateForm(
                            userDashboardActiveApplication
                              ?.admission_application?.[0]
                          )
                        }
                        customStyle={{
                          padding: "0.4rem 0.7rem",
                          fontSize: "16px",
                          "--ccb-bg": "#e6686830",
                          "--ccb-b": "#ed4a4a",
                          "--ccb-text": "#f00b0b",
                          "--ccb-bg-h": "#eb3232",
                          "--ccb-text-h": "#ffffff",
                          justifyContent: "center",
                        }}
                      />
                    ) : (
                      <>
                        {userDashboardActiveApplication
                          ?.admission_application?.[0]?.student
                          ?.application_print?.[0]?.value ? (
                          <a
                            href={`${pdfCdnUrl}/${userDashboardActiveApplication?.admission_application?.[0]?.student?.application_print?.[0]?.value}`}
                            title="Download Admission Application File"
                            target="_blank"
                            rel="noopener noreferrer"
                            // style={{
                            //   color: "#00f",
                            // }}
                          >
                            <div
                              className={style.ad_button}
                              style={{
                                marginTop: "0",
                              }}
                            >
                              Download Admission Application Form
                              <img
                                src={`${assestsUserAdmissionUrl}/download.svg`}
                                alt="download"
                              />
                            </div>
                          </a>
                        ) : null}
                      </>
                    )}
                  </>
                )}
              </>
            )}
          </div>
        </div>
      )}
      {pay && (
        <>
          {userDashboardActiveApplication?.fees_payment?.[0]?.is_hostel ? (
            <StudentHostelPayFees
              onClose={() => setPay(false)}
              admission={
                userDashboardActiveApplication?.fees_payment?.[0]
                  ?.remaining_list
              }
              install={
                userDashboardActiveApplication?.fees_payment?.[0]
                  ?.remaining_list?.applicable_card?.remaining_array?.[0]
              }
              isEditable={
                userDashboardActiveApplication?.fees_payment?.[0]?.student
                  ?.online_amount_edit_access === "Granted"
                  ? true
                  : false
              }
              statusId={userDashboardActiveApplication?.fees_payment?.[0]?._id}
              isStatus
            />
          ) : (
            <StudentAdmissionPayFees
              onClose={() => setPay(false)}
              admission={
                userDashboardActiveApplication?.fees_payment?.[0]
                  ?.remaining_list
              }
              install={
                userDashboardActiveApplication?.fees_payment?.[0]
                  ?.remaining_list?.applicable_card?.remaining_array?.[0]
              }
              isEditable={
                userDashboardActiveApplication?.fees_payment?.[0]?.student
                  ?.online_amount_edit_access === "Granted"
                  ? true
                  : false
              }
              statusId={userDashboardActiveApplication?.fees_payment?.[0]?._id}
              isStatus
            />
          )}
        </>
      )}
      {disabled && <QvipleLoader />}
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default memo(FeedCurrentAdmissionApplication);
